
#app {
  #font-family: 'Avenir', Helvetica, Arial, sans-serif;
  #color: #2c3e50;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}




































































.menu-label {
  color: #fff000;
  font-size: 12pt;
}
.hero-body {
  position: relative;
}
.hero-body div {
    width: 30vw;
}
.hero-body div.presentation {
      margin-left: 10vw;
      width: 45vw;
}
@media screen and (max-width: 48em) {
.hero-body {
    display: block !important;
    height: calc(200vh + 6rem);
    width: 100vw;
    min-width: 100vw !important;
}
.hero-body div {
      height: 100vh;
      width: calc(100vw - 3em);
      margin-left: 3vw;
}
.hero-body div.presentation {
        padding-top: 30vh;
        top: 20%;
}
.hero-body div .title.is-1 {
        font-size: 1.5rem;
}
.hero-body div .subtitle.is-3 {
        font-size: 1rem;
}
